.products{
    background-image: url(../images/background/background-mobile.png);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    margin: 0 auto;
    padding-bottom: 60px;
    padding-top: 60px;
}
.container-products-conttainer {
    margin: 0 auto;
}
.product-label{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin-bottom: 12px;
}
.products-button{
    border: none;
    background-color: transparent;
    margin-bottom: 0;
    height: 16px;
    margin-bottom: 16px;
}
.arrow-svg{
    width: 40px;
    height: 16px;
}
.products-ul{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    gap: 168px;
    border-radius: 24px;
    position: relative;
}
.products-ul li{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    text-align: center;
    border-radius: 24px;
    width: 85vw;
}
.milkshakes-h3{
    margin-top: 123px;
}
.ice-coffee-h3{
    margin-top: 123px;
}
.ice-cream-h3{
    margin-top: 123px;
}
.ice-coffee-p {
  margin-top: 40px;
  margin-bottom: 61px;
  margin-right: 40px;
  margin-left: 40px;
  width: 200px;
}
.ice-cream-p {
  margin-top: 40px;
  margin-bottom: 109px;
  margin-right: 40px;
  margin-left: 40px;
  width: 200px;
}
.milkshakes-p {
  margin-top: 40px;
  margin-bottom: 81px;
  margin-right: 40px;
  margin-left: 40px;
  width: 200px;
}
// fonts
.products-ul li p {
  font-family: 'Montserrat', sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.78;
  letter-spacing: 0.04em;
  text-align: center;
  color: var(--dark-txt-color);
}
.products-ul li h3 {
    font-family: 'Montserrat', sans-serif;
    font-size: 20px;
    font-weight: 700;
    line-height: 1.2;
    letter-spacing: 0.04em;
    text-align: center;
    color: var(--dark-txt-color);
}
.product-label{
    font-family: 'Montserrat', sans-serif;
    font-size: 18px;
    font-weight: 800;
    line-height: 1.2;
    text-align: center;
    color: var(--dark-txt-color);
}
.h2products{
    font-family: 'Seymour One', sans-serif;
    font-size: 30px;
    font-weight: 400;
    line-height: 1.25;
    letter-spacing: 0.06em;
    text-align: center;
    color: var(--dark-txt-color);
    text-transform: uppercase;
    margin-bottom: 145px;
}
// fonts
.ice-cream-li {
  background-color: var(--product-first-color);
  position: relative;
}
.ice-coffee-li {
  background-color: var(--product-second-color);
  position: relative;
}
.milkshakes-li {
  background-color: var(--product-third-color);
  position: relative;
}
.label {
  display: flex;
  align-items: center;
  justify-content: center;
}
// buttons
.ice-cream-button {
  background-color: transparent;
  border: none;
}
.ice-coffee-button {
  background-color: transparent;
  border: none;
}
.milkshakes-button {
  background-color: transparent;
  border: none;
}

// buttons
// before
.ice-cream-li::before {
  content: '';
  width: 200px;
  height: 200px;
  position: absolute;
  top: -100;
  z-index: 2;
  background-image: url(../images/products/ice-cream-mob+tab.png);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  // box-shadow: 0px 5.34219px 20.0332px rgba(10, 136, 23, 0.25);
}
.ice-coffee-li::before {
  content: '';
  background-image: url(../images/products/ice-coffee-mob+tab.png);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  width: 200px;
  height: 200px;
  position: absolute;
  top: -100;
  z-index: 2;
}
.milkshakes-li::before {
  content: '';
  background-image: url(../images/products/milkshakes-mob+tab.png);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  width: 200px;
  height: 200px;
  position: absolute;
  top: -100;
  z-index: 2;
}
// before

// shadow
.shadow-ice-cream {
  width: 200px;
  height: 200px;
  background: linear-gradient(
      0deg,
      rgba(156, 190, 143, 0.7),
      rgba(156, 190, 143, 0.7)
    ),
    #d9d9d9;
  box-shadow: 0px 8px 30px rgba(10, 136, 23, 0.25);
  border-radius: 50%;
  position: absolute;
  top: -89px;
  overflow: hidden;
}
.shadowiice-coffee {
  width: 200px;
  height: 200px;
  border-radius: 50%;
  background: linear-gradient(
      0deg,
      rgba(248, 210, 78, 0.7),
      rgba(248, 210, 78, 0.7)
    ),
    #d9d9d9;
  box-shadow: 0px 8px 30px rgba(153, 114, 23, 0.25);
  position: absolute;
  top: -89px;
  overflow: hidden;
}
.shadow-milkshakes {
  width: 200px;
  height: 200px;
  border-radius: 50%;
  background: linear-gradient(
      0deg,
      rgba(213, 117, 63, 0.7),
      rgba(213, 117, 63, 0.7)
    ),
    #d9d9d9;
  box-shadow: 0px 8px 30px rgba(0, 0, 0, 0.25);
  position: absolute;
  top: -89px;
  overflow: hidden;
}
// shadow

