*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --dark-txt-color: #131313;
  --light-txt-color: #fcfcfc;
  --contacts-hover-txt-color: #d6936d;
  --footer-copyright-txt-color: #7a7a7a;
  --dark-bg-color: #84a178;
  --light-bg-color: #fcfcfc;
  --product-first-color: #aec6a5;
  --product-second-color: #efd478;
  --product-third-color: #d6936d;
  --modal-ingredients-color: #9cbe8f;
  --default-accent-color: #f8d24e;
  --hover-accent-color: #0a8817;
  --disabled-accent-color: #ababab;
  --main-font: "Montserrat", sans-serif;
  --secondary-font: "Seymour One", sans-serif;
  --transition-dur-and-func: 250ms cubic-bezier(.4, 0, .2, 1);
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

ul, ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

button {
  cursor: pointer;
  font-family: inherit;
  font-style: inherit;
  border: none;
}

a {
  text-decoration: none;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

body {
  font-family: var(--main-font);
}

@media screen and (min-width: 320px) {
  .container {
    width: 320px;
    margin-left: auto;
    margin-right: auto;
    padding-left: 20px;
    padding-right: 20px;
  }
}

@media screen and (min-width: 480px) {
  .container {
    width: 480px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    width: 1200px;
    padding-left: 16px;
    padding-right: 16px;
  }
}

html {
  scroll-behavior: smooth;
}

.header {
  background-color: var(--dark-bg-color);
}

.header-container {
  justify-content: space-between;
  align-items: center;
  padding-top: 12px;
  display: flex;
}

.header-logo {
  align-items: flex-start;
  display: flex;
  position: relative;
}

.logo-text {
  font-family: var(--secondary-font);
  text-align: center;
  letter-spacing: -.1em;
  color: var(--dark-bg-color);
  -webkit-text-stroke: .884373px var(--default-accent-color);
  font-size: 15px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.26;
  position: absolute;
  top: 21px;
  left: 15px;
}

.header-icon {
  fill: var(--default-accent-color);
  width: 19px;
  height: 37px;
}

.mobile-menu-open {
  background-color: rgba(0, 0, 0, 0);
  border: none;
  margin-left: auto;
  padding: 0;
  line-height: 0;
}

.menu-burger-icon {
  width: 32px;
  height: 32px;
  fill: var(--light-txt-color);
}

.navigation-list, .header-button {
  display: none;
}

.mobile-menu {
  width: 100%;
  height: 100%;
  z-index: 20;
  background-color: var(--dark-bg-color);
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transition: opacity var(--transition-dur-and-func), transform var(--transition-dur-and-func);
  position: fixed;
  top: 0;
  left: 0;
  transform: translateX(100%);
}

.is-open {
  opacity: 1;
  visibility: visible;
  pointer-events: auto;
  transform: translateX(0);
}

.modal-burger-icon {
  width: 19px;
  height: 19px;
  fill: var(--light-txt-color);
}

.modal-burger-button {
  background-color: rgba(0, 0, 0, 0);
  border: none;
  padding: 0;
  line-height: 0;
}

.mobile-menu-navigation-item {
  text-align: center;
}

.mobile-menu-navigation-link {
  color: var(--light-txt-color);
  transition: color var(--transition-dur-and-func);
  font-size: 32px;
  font-style: normal;
  font-weight: 700;
  line-height: 1.21;
}

.mobile-menu-navigation-link:hover, .mobile-menu-navigation-link:disabled {
  color: var(--dark-txt-color);
}

.mobile-menu-navigation-item:not(:last-child) {
  margin-bottom: 32px;
}

.mobile-menu-container {
  height: 100%;
  flex-direction: column;
  padding: 12px 20px 64px 32px;
  display: flex;
}

.mobile-menu-header {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 51px;
  display: flex;
}

.mobile-menu-button {
  width: 152px;
  height: 42px;
  color: var(--dark-txt-color);
  background-color: var(--default-accent-color);
  text-align: center;
  transition: background-color var(--transition-dur-and-func), color var(--transition-dur-and-func);
  border-radius: 10px;
  font-size: 14px;
  font-style: normal;
  font-weight: 600;
  line-height: 17px;
}

.mobile-menu-button:hover {
  background-color: var(--hover-accent-color);
}

.mobile-menu-button:focus {
  background-color: var(--hover-accent-color);
  color: var(--light-txt-color);
}

.mobile-menu-button:disabled {
  background-color: var(--disabled-accent-color);
  color: var(--dark-txt-color);
}

.header-button-icon {
  width: 6px;
  height: 10px;
  fill: currentColor;
}

.mobile-menu-footer {
  justify-content: center;
  align-items: center;
  margin-top: auto;
  display: flex;
}

