.backdrop {
  z-index: 1000;
  width: 100%;
  height: 100%;
  opacity: 1;
  background-color: rgba(0, 0, 0, .4);
  justify-content: center;
  padding-top: 20px;
  padding-bottom: 20px;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}

.backdrop.is-hidden .buy-now-modal {
  transform: translate(-50%, -50%)scale(.8);
}

.backdrop-buy-now {
  justify-content: center;
  padding-top: 20px;
  padding-bottom: 20px;
  display: flex;
}

.buy-now-modal {
  width: 280px;
  background-color: var(--light-bg-color);
  border: 2px solid var(--default-accent-color);
  transition: transform var(--transition-dur-and-func), opacity var(--transition-dur-and-func);
  -webkit-overflow-scrolling: touch;
  transition: transform var(--transition-dur-and-func);
  border-radius: 9px;
  padding: 28px;
  position: relative;
  overflow: auto;
}

@media screen and (min-width: 768px) {
  .buy-now-modal {
    max-width: 568px;
    max-height: 644px;
    width: 95%;
    height: 95%;
