    left: 50%;
    transform: translate(-50%, -50%)scale(1);
  }
}

.buy-now-modal__close-btn {
  width: 13px;
  height: 13px;
  transform: background-color, color;
  transition: fill var(--transition-dur-and-func);
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid rgba(0, 0, 0, 0);
  border-radius: 50%;
  outline: rgba(0, 0, 0, 0);
  justify-content: center;
  align-items: center;
  padding: 0;
  display: flex;
  position: absolute;
  top: 20px;
  right: 20px;
}

.buy-now-modal__close-btn:hover, .buy-now-modal__close-btn:focus {
